
/**
 *  v-preventClick
 * @module utils/directives/preventClick
 * @author risheng.huang
 * v-preventClick="()" 2000(2s) 
 * <el-button type="primary" @click="submitForm('ruleForm')" v-preventClick size="mini"> </el-button>
 * 
 */

export default {
    install(Vue) {
        Vue.directive('preventClick', {
            inserted(el, binding) {
                el.addEventListener('click', () => {
                    if (!el.disabled) {
                        el.disabled = true
                        setTimeout(() => {
                            el.disabled = false
                        }, binding.value || 2000)
                    }
                })
            }
        })
    }
}